import { _decorator, AudioClip, AudioSource, Button, Component, director } from 'cc';
const { ccclass, requireComponent, property } = _decorator;

@ccclass('GlobalButtonAudio')
@requireComponent(AudioSource)
export class GlobalButtonAudio extends Component {
  @property(AudioClip)
  public buttonClickAudioClip: AudioClip = null;

  private audioSource: AudioSource = null;

  protected onLoad(): void {
    director.addPersistRootNode(this.node);

    this.audioSource = this.getComponent(AudioSource);
    this.audioSource.clip = this.buttonClickAudioClip;

    if (Button.prototype["touchBeganClone"])
      return;

    Button.prototype["touchBeganClone"] = Button.prototype["_onTouchEnded"];
    const that = this;
    Button.prototype["_onTouchEnded"] = function (event) {
      if (this.interactable && this.enabledInHierarchy) {
        that.audioSource.play();
      }
      this.touchBeganClone(event);
    }
  }
}

