import { _decorator, Color, Component, EventMouse, instantiate, log, Node, Prefab, Sprite } from 'cc';
import { GameEvent } from './GameEvent';
const { ccclass, property } = _decorator;

@ccclass('PalletCtrl')
export class PalletCtrl extends Component {
  @property(Prefab)
  public colorPrefab: Prefab = null;

  private _colors: Color[] = [];

  protected start(): void {
    const colorsJson = localStorage.getItem('colors');
    const colors = JSON.parse(colorsJson);
    if (colors) {
      for (const c of colors) {
        let color = new Color();
        this.addColor(Color.fromHEX(color, c));
      }
    }
  }

  public addColor(color: Color) {
    this.addColorNode(color);
    this.save();
  }

  private addColorNode(color: Color) {
    this._colors.push(color);

    const node = instantiate(this.colorPrefab);
    node.getComponent(Sprite).color = color;
    node.on(Node.EventType.MOUSE_DOWN, (event: EventMouse) => {
      if (event.getButton() === EventMouse.BUTTON_LEFT) {
        const color = node.getComponent(Sprite).color;
        GameEvent.target.emit(GameEvent.SELECT_COLOR, color);
      } else if (event.getButton() === EventMouse.BUTTON_RIGHT) {
        this._colors.splice(this.node.children.indexOf(node), 1);
        this.save();

        this.node.removeChild(node);
      }
    }, this);

    this.node.addChild(node);
  }

  private save() {
    localStorage.setItem('colors', JSON.stringify(this._colors.map(c => c.toHEX())));
  }

  private onColorCellClicked(event: EventMouse) {
    event.currentTarget
  }
}


