// 假设NonogramDescription和NonogramSolution类已经在其他地方定义
import { CellState } from './CellState';
import { NonogramDescription } from './NonogramDescription';
import { NonogramSolution } from './NonogramSolution';
import { SolveResult } from './SolveResult';

export class Solver {
  private width: number;
  private height: number;
  private cells: CellState[][];
  private descr: NonogramDescription;
  private solveResult: SolveResult;
  private curRow: CellState[];
  private curRowBits: number;
  private curRowFilled: number;  // True if necessarily filled.
  private curRowNotEmpty: number;  // False if necessarily empty.
  private curRowLength: number;
  private curDescription: number[];
  private curRowImpossible: boolean;
  private verbose: boolean = false;
  private counterExample: NonogramSolution | null = null;

  constructor(descr: NonogramDescription) {
    this.width = descr.width;
    this.height = descr.height;
    this.descr = descr;
    this.cells = Array(this.width)
      .fill(0)
      .map(() => Array(this.height)
        .fill(CellState.NOT_DECIDED));
    this.solveResult = SolveResult.NOT_ATTEMPTED;
  }

  private solveRow(row: CellState[], descr: number[]): boolean {
    if (this.verbose) {
      console.log(`Solving row: ${descr.join(' ')}   ${row.map(cs => cs).join('')}\n`);
    }

    this.curRow = row;
    this.curDescription = descr;
    this.curRowLength = row.length;
    this.curRowFilled = (1 << this.curRowLength) - 1;

    this.curRowNotEmpty = 0;
    this.curRowBits = 0;

    if (!this.solveRowRec(0, 0)) {
      this.curRowImpossible = true;
      return false;
    }

    let changed = false;
    for (let x = 0; x < this.curRowLength; x++) {
      if (row[x] === CellState.NOT_DECIDED) {
        if ((this.curRowFilled & (1 << x)) !== 0) {
          row[x] = CellState.FILLED;
          changed = true;
        } else if ((this.curRowNotEmpty & (1 << x)) === 0) {
          row[x] = CellState.EMPTY;
          changed = true;
        }
      }
    }

    if (changed && this.verbose) {
      console.log(`Changed to: ${row.map(cs => cs).join('')}`);
    }

    return changed;
  }

  private solveRowRec(pos: number, numsUsed: number): boolean {
    if (numsUsed === this.curDescription.length) {
      for (let x = pos; x < this.curRowLength; x++) {
        if (this.curRow[x] === CellState.FILLED) return false;
      }
      this.curRowFilled &= this.curRowBits;
      this.curRowNotEmpty |= this.curRowBits;
      return true;
    }

    let curLen = this.curDescription[numsUsed];
    let feasible = false;
    for (let x = pos; x < this.curRowLength; x++) {
      if (x + curLen > this.curRowLength) break;

      let canBeFilled = true;
      for (let i = x; i < x + curLen; i++) {
        if (this.curRow[i] === CellState.EMPTY) {
          canBeFilled = false;
          break;
        }
      }

      if (canBeFilled && x + curLen < this.curRowLength && this.curRow[x + curLen] === CellState.FILLED) {
        canBeFilled = false;
      }

      if (canBeFilled) {
        for (let i = x; i < x + curLen; i++) this.curRowBits |= (1 << i);
        let emptyCell = (x + curLen === this.curRowLength) ? 0 : 1;
        if (this.solveRowRec(x + curLen + emptyCell, numsUsed + 1)) feasible = true;
        for (let i = x; i < x + curLen; i++) this.curRowBits &= ~(1 << i);
      }

      if (this.curRow[x] === CellState.FILLED) break;
    }

    return feasible;
  }

  private solveStep(): boolean {
    let changed = false;

    for (let x = 0; x < this.width; x++) {
      let col = this.cells[x];
      if (this.solveRow(col, this.descr.getColumnDescription(x))) {
        changed = true;
        for (let y = 0; y < this.height; y++) this.cells[x][y] = col[y];
      }
      if (this.curRowImpossible) return false;
    }

    for (let y = 0; y < this.height; y++) {
      let row: CellState[] = [];
      for (let x = 0; x < this.width; x++) {
        row.push(this.cells[x][y]);
      }
      if (this.solveRow(row, this.descr.getRowDescription(y))) {
        changed = true;
        for (let x = 0; x < this.width; x++) this.cells[x][y] = row[x];
      }
      if (this.curRowImpossible) return false;
    }

    return changed;
  }

  private solveRec(): SolveResult {
    this.curRowImpossible = false;
    while (true) {
      let changed = this.solveStep();
      if (this.curRowImpossible) return SolveResult.IMPOSSIBLE;
      if (!changed) break;
    }

    let solved = true;
    let badX = 0, badY = 0;
    for (let x = 0; x < this.width; x++) {
      for (let y = 0; y < this.height; y++) {
        if (this.cells[x][y] === CellState.NOT_DECIDED) {
          solved = false;
          badX = x;
          badY = y;
          break;
        }
      }
    }

    if (solved) return SolveResult.SOLVED;

    let backupCells = this.cloneCells();
    this.cells[badX][badY] = CellState.EMPTY;
    if (this.solveRec() === SolveResult.SOLVED) {
      return SolveResult.SOLVED;
    } else {
      this.cells = backupCells;
      this.cells[badX][badY] = CellState.FILLED;
      return this.solveRec();
    }
  }

  public solve(): NonogramSolution | null {
    if (this.solveRec() === SolveResult.IMPOSSIBLE) {
      this.solveResult = SolveResult.IMPOSSIBLE;
      return null;
    } else {
      this.solveResult = SolveResult.SOLVED;
      return NonogramSolution.createFromCellStates(this.cells);
    }
  }

  private cloneCells(): CellState[][] {
    let ret: CellState[][] = Array(this.width).fill(0).map(() => Array(this.height).fill(CellState.NOT_DECIDED));
    for (let x = 0; x < this.width; x++) {
      for (let y = 0; y < this.height; y++) {
        ret[x][y] = this.cells[x][y];
      }
    }
    return ret;
  }

  private flipState(x: CellState): CellState {
    if (x === CellState.FILLED) return CellState.EMPTY;
    else if (x === CellState.EMPTY) return CellState.FILLED;
    else return x;
  }

  public setVerbose(verbose: boolean): void {
    this.verbose = verbose;
  }

  public getCounterExample(): NonogramSolution | null {
    return this.counterExample;
  }

  public hasUniqueSolution(): boolean {
    if (this.solveResult === SolveResult.NOT_ATTEMPTED) {
      throw new Error("Solution haven't been performed.");
    }

    if (this.solveResult === SolveResult.IMPOSSIBLE) {
      return false;
    }

    if (this.solveResult === SolveResult.AMBIGUOUS) {
      return false;
    }

    const backupCells = this.cloneCells();
    let unique = true;
    for (let x = 0; x < this.width; x++) {
      for (let y = 0; y < this.height; y++) {
        for (let x1 = 0; x1 < this.width; x1++) {
          for (let y1 = 0; y1 < this.height; y1++) {
            this.cells[x1][y1] = CellState.NOT_DECIDED;
            this.cells[x][y] = this.flipState(backupCells[x][y]);

            const res = this.solveRec();
            if (res != SolveResult.IMPOSSIBLE) {
              this.counterExample = NonogramSolution.createFromCellStates(this.cells);
              unique = false;
            }
          }
        }
      }
    }
    this.cells = backupCells;
    return unique;
  }
}