import { sys } from "cc";
import { log } from "cc";
import { random } from "cc";
import { BaseMiniGameAd } from "./BaseMiniGameAd";
import { DouYinMiniGameAd } from "./DouYinMiniGameAd";
import { VivoMiniGameAd } from "./VivoMiniGameAd";
import { OppoMiniGameAd } from "./OppoMiniGameAd";
import { HuaWeiMiniGameAd } from "./HuaWeiMiniGameAd";
import { XiaoMiMiniGameAd } from "./XiaoMiMiniGameAd";

export class MiniGameAdMgr {
  private static _instance: MiniGameAdMgr = null;

  private _ad: BaseMiniGameAd = null;

  public static get instance(): MiniGameAdMgr {
    if (this._instance == null) {
      this._instance = new MiniGameAdMgr();
      if (sys.platform === sys.Platform.BYTEDANCE_MINI_GAME) {
        this._instance._ad = new DouYinMiniGameAd();
      } else if (sys.platform === sys.Platform.HUAWEI_QUICK_GAME) {
        this._instance._ad = new HuaWeiMiniGameAd();
      } else if (sys.platform === sys.Platform.XIAOMI_QUICK_GAME) {
        this._instance._ad = new XiaoMiMiniGameAd();
      } else if (sys.platform === sys.Platform.OPPO_MINI_GAME) {
        this._instance._ad = new OppoMiniGameAd()
      } else if (sys.platform === sys.Platform.VIVO_MINI_GAME) {
        this._instance._ad = new VivoMiniGameAd();
      } else {
        throw new Error(`Unsupported MiniGameAd platform: ${sys.platform}`);
      }
    }
    return this._instance;
  }

  /**
   * 展示激励广告
   * @param adUnitId 
   */
  public showRewardAd(adUnitId: string,
    loadCallback: (err: Error) => void,
    closeCallback: (err: Error, res: { isEnded: boolean }) => void) {
    this._ad.showRewardAd(adUnitId, loadCallback, closeCallback);
  }

  public showInterstitialAd(adUnitId: string, rate: number = 1.0, delay: number = 0) {
    if (random() > rate) {
      return;
    }

    this._ad.showInterstitialAd(adUnitId);
  }
}

