import { _decorator, Button, Color, Component, EditBox, instantiate, log, Node, Prefab, Size, Sprite, sys, Toggle, ToggleContainer, UITransform } from 'cc';
import { LevelChessBoardCtrl } from './LevelChessBoardCtrl';
import { PalletCtrl } from './PalletCtrl';
import { GameEvent } from './GameEvent';
import { NonogramSolution } from '../solution/NonogramSolution';
import { NonogramDescription } from '../solution/NonogramDescription';
import { Solver } from '../solution/Solver';
const { ccclass, property } = _decorator;

@ccclass('LevelEditor')
export class LevelEditor extends Component {
  @property(Toggle)
  public level5x5Toggle: Toggle = null;

  @property(Toggle)
  public level10x10Toggle: Toggle = null;

  @property(Toggle)
  public level15x15Toggle: Toggle = null;

  @property(EditBox)
  public colorEditBox: EditBox = null;

  @property(PalletCtrl)
  public palletCtrl: PalletCtrl = null;

  @property(Sprite)
  public currentColorSprite: Sprite = null;

  @property(Toggle)
  public puzzleModeToggle: Toggle = null;

  @property(Node)
  public operationContainer: Node = null;

  @property(LevelChessBoardCtrl)
  public chessBoardCtrl: LevelChessBoardCtrl = null;

  protected start(): void {
    GameEvent.target.on(GameEvent.SELECT_COLOR, this.onSelectColor, this);
    this.updateMode();
  }

  protected onDestroy(): void {
    GameEvent.target.off(GameEvent.SELECT_COLOR, this.onSelectColor, this);
  }

  public new5x5() {
    this.chessBoardCtrl.newLevel(5, 5);
  }

  public new10x10() {
    this.chessBoardCtrl.newLevel(10, 10);
  }

  public new15x15() {
    this.chessBoardCtrl.newLevel(15, 15);
  }

  //#region UI Events

  private onNewLevelButtonClicked() {
    if (this.level5x5Toggle.isChecked) {
      this.new5x5();
    } else if (this.level10x10Toggle.isChecked) {
      this.new10x10();
    } else if (this.level15x15Toggle.isChecked) {
      this.new15x15();
    }

    this.puzzleModeToggle.isChecked = false;
    this.updateMode();
  }

  private onAddColorButtonClicked() {
    if (!this.colorEditBox.string) {
      return;
    }

    let color = new Color();
    Color.fromHEX(color, this.colorEditBox.string);
    this.palletCtrl.addColor(color);
  }

  private onClearAllColorsButtonClicked() {

  }

  /**
   * 切换谜题模式
   * @param toggle 
   */
  private onPuzzleToggleChecked(toggle: Toggle) {
    this.chessBoardCtrl.setPuzzleMode(toggle.isChecked);
    this.updateMode();
  }

  /**
   * 同步原图像素
   */
  private onSyncPixelsButtonClicked() {
    this.chessBoardCtrl.syncPixels();
  }

  /**
   * 生成谜题
   */
  private onGenerateButtonClicked() {
    const pixels = transposeMatrix(this.chessBoardCtrl.puzzlePixels);
    log(pixels);

    const solution = NonogramSolution.createFromPixels(pixels);
    log(solution);
    const description = NonogramDescription.createWithSolution(solution);
    log(description);

    const solver = new Solver(description);
    solver.solve();
    if (!solver.hasUniqueSolution()) {
      log('当前谜题不符合要求：没有唯一解');
      return;
    }
    
    log('生成谜题完成！')
  }

  //#endregion

  private onSelectColor(color: Color) {
    this.currentColorSprite.color = color;
    this.chessBoardCtrl.setColor(color);
  }

  private updateMode() {
    this.operationContainer.active = this.puzzleModeToggle.isChecked;
  }
}

function transposeMatrix(matrix: boolean[][]): boolean[][] {
  return matrix[0].map((_, colIndex: number) => matrix.map((row: boolean[]) => row[colIndex]));
}