import { _decorator, Component, director, Node } from 'cc';
const { ccclass, property } = _decorator;

export class SceneMgr {
  private static _instance: SceneMgr | null = null;

  private _sceneStack: string[] = null;

  private _sceneToData: Map<string, any> = null;

  public static get instance(): SceneMgr {
    if (this._instance == null) {
      this._instance = new SceneMgr();
      this._instance._sceneStack = [];
      this._instance._sceneToData = new Map();
    }
    return this._instance;
  }

  public init() {

  }

  public pushScene(scene: string, data: any = null) {
    this._sceneStack.push(scene);
    this._sceneToData.set(scene, data);
    director.loadScene(scene);
  }

  public switchScene(scene: string, data: any = null) {
    this._sceneStack.pop();
    this._sceneStack.push(scene);
    this._sceneToData.set(scene, data);
    director.loadScene(scene);
  }

  public popScene(data: any = null) {
    this._sceneStack.pop();
    const scene = this._sceneStack[this._sceneStack.length - 1];
    this._sceneToData.set(scene, data);
    director.loadScene(scene);
  }

  public popToScene(scene: string, data: any = null) {
    while (
      this._sceneStack.length > 0 &&
      this._sceneStack[this._sceneStack.length - 1] !== scene
    ) {
      this._sceneStack.pop();
    }
    this._sceneToData.set(scene, data);
    director.loadScene(scene);
  }

  public getSceneData(scene: string) {
    return this._sceneToData.get(scene);
  }
}


