import { log } from 'cc';
import { native } from 'cc';
import { _decorator, Component, Node, sys } from 'cc';

export class PlatformUtils {
  /**
   * 是否是小游戏，用于避免冲突，包括：
   * - 游戏 UI 设计（右上角按钮）
   * - 不支持 openUrl
   */
  public static get isMiniGame() {
    return sys.platform === sys.Platform.WECHAT_GAME ||
      sys.platform === sys.Platform.BYTEDANCE_MINI_GAME ||
      sys.platform === sys.Platform.HUAWEI_QUICK_GAME ||
      sys.platform === sys.Platform.XIAOMI_QUICK_GAME ||
      sys.platform === sys.Platform.OPPO_MINI_GAME ||
      sys.platform === sys.Platform.VIVO_MINI_GAME;
  }

  /**
   * 是否是国内安卓快游戏，用于调用快游戏接口
   */
  public static get isQuickGame() {
    return sys.platform === sys.Platform.HUAWEI_QUICK_GAME ||
      sys.platform === sys.Platform.XIAOMI_QUICK_GAME ||
      sys.platform === sys.Platform.OPPO_MINI_GAME ||
      sys.platform === sys.Platform.VIVO_MINI_GAME;
  }

  /**
   * 判断是否是华为原生平台
   */
  public static get isHuaWeiApp() {
    return this.getPackageName()?.endsWith('huawei');
  }

  /**
   * 判断是否是小米原生平台
   */
  public static get isXiaomiApp() {
    return this.getPackageName()?.endsWith('mi');
  }

  /**
   * 判断是否是 Oppo 原生平台
   */
  public static get isOppoApp() {
    return this.getPackageName()?.endsWith('nearme.gamecenter');
  }

  /**
   * 判断是否是 Vivo 原生平台
   */
  public static get isVivoApp() {
    return this.getPackageName()?.endsWith('vivo');
  }

  /**
   * 获取 Android 包名
   * @returns 
   */
  public static getPackageName() {
    if (sys.platform === sys.Platform.ANDROID) {
      return native.reflection.callStaticMethod('com/ihg/common/PackageUtils',
        'getPackageName',
        '()Ljava/lang/String;');
    }

    return null;
  }
}

