import { CellState } from './CellState';

export class NonogramSolution {
  public static readonly EMPTY_MARKER = ' ';
  public static readonly FILL_MARKER = 'X';

  private _width: number;
  private _height: number;
  private _pixels: boolean[][];

  constructor(width: number, height: number, pixels: boolean[][]) {
    this._width = width;
    this._height = height;
    this._pixels = pixels;
  }

  public static createFromCellStates(cells: CellState[][]): NonogramSolution {
    const width = cells.length;
    const height = cells[0].length;
    const pixels = Array(width)
      .fill(false)
      .map(() => Array(height)
        .fill(false));
    for (let x = 0; x < width; x++) {
      for (let y = 0; y < height; y++) {
        pixels[x][y] = cells[x][y] === CellState.FILLED;
      }
    }

    return new NonogramSolution(width, height, pixels);
  }

  public static createFromPixels(pixels: boolean[][]): NonogramSolution {
    const width = pixels.length;
    const height = pixels[0].length;

    return new NonogramSolution(width, height, pixels);
  }

  static random(width: number, height: number, prob: number): NonogramSolution {
    const pixels = Array(width).fill(false).map(() => Array(height).fill(false));
    for (let x = 0; x < width; x++) {
      for (let y = 0; y < height; y++) {
        pixels[x][y] = Math.random() < prob;
      }
    }
    return new NonogramSolution(width, height, pixels);
  }

  public get width(): number {
    return this._width;
  }

  public get height(): number {
    return this._height;
  }

  public getPixel(x: number, y: number): boolean {
    return this._pixels[x][y];
  }

  public setPixel(x: number, y: number, value: boolean): void {
    this._pixels[x][y] = value;
  }

  public getRow(y: number): boolean[] {
    return this._pixels.map(x => x[y]);
  }

  public getColumn(x: number): boolean[] {
    return this._pixels[x];
  }

  public getRowDescription(y: number): number[] {
    let description: number[] = [];
    let count = 0;
    for (let x = 0; x < this._width; x++) {
      if (this._pixels[x][y]) {
        count++;
      } else if (count > 0) {
        description.push(count);
        count = 0;
      }
    }
    if (count > 0) {
      description.push(count);
    }
    return description;
  }

  public getColumnDescription(x: number): number[] {
    let description: number[] = [];
    let count = 0;
    for (let y = 0; y < this._height; y++) {
      if (this._pixels[x][y]) {
        count++;
      } else if (count > 0) {
        description.push(count);
        count = 0;
      }
    }
    if (count > 0) {
      description.push(count);
    }
    return description;
  }

  public toString(): string {
    let result = '';
    for (let y = 0; y < this._height; y++) {
      for (let x = 0; x < this._width; x++) {
        result += this._pixels[x][y] ? NonogramSolution.FILL_MARKER : NonogramSolution.EMPTY_MARKER;
      }
      result += '\n';
    }
    return result;
  }

  public equals(other: NonogramSolution): boolean {
    if (this._width !== other._width || this._height !== other._height) {
      return false;
    }
    for (let x = 0; x < this._width; x++) {
      for (let y = 0; y < this._height; y++) {
        if (this._pixels[x][y] !== other.getPixel(x, y)) {
          return false;
        }
      }
    }
    return true;
  }
}