import { _decorator, Component, Label, Node } from 'cc';
const { ccclass, property } = _decorator;
import { NonogramDescription } from './NonogramDescription';
import { Solver } from './Solver';

@ccclass('SolutionScene')
export class SolutionScene extends Component {
  @property(Label)
  private label: Label = null!;

  async start() {
    const desc = await NonogramDescription.fromFile('test.non');

    const solver = new Solver(desc);
    // solver.setVerbose(true);
    const solution = solver.solve();
    // console.log(solution);
    // console.log(solution.toString());
    // console.log(solver.hasUniqueSolution());

    this.label.string = solution.toString();
  }
}


