import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('DialogCtrl')
export class DialogCtrl extends Component {
  protected _data: any = null;

  protected _callback: (code: number, data: any) => void = null;

  private _dismissWhenClickedMask: boolean = false;

  public set dismissWhenClickedMask(val: boolean) {
    this._dismissWhenClickedMask = val;
  }

  public get dismissWhenClickedMask() {
    return this._dismissWhenClickedMask;
  }

  public setData(data: any) {
    this._data = data;
  }

  public setCallback(callback: (code: number, data: any) => void) {
    this._callback = callback;
  }

  public close() {
    this.node.destroy();
  }
}


