/**
 * 广告桥接
 */
export class AdBridge {
  /**
   * 预加载激励广告
   * @param posId 广告位 id
   */
  public static readonly PRELOAD_REWARD_AD = 'ccams_s2n_preload_reward_ad';
  /**
   * 展示激励广告
   * @param posId 广告位 id
   */
  public static readonly SHOW_REWARD_AD = 'ccams_s2n_show_reward_ad';
  /**
   * 激励广告加载中
   */
  public static readonly REWARD_AD_LOADING = 'ccams_n2s_reward_ad_loading';
  /**
   * 激励广告加载完成
   */
  public static readonly REWARD_AD_LOAD_SUCCESS = 'ccams_n2s_reward_ad_load_success';
  /**
   * 激励广告加载失败
   */
  public static readonly REWARD_AD_LOAD_FAILED = 'ccams_n2s_reward_ad_load_failed';
  /**
   * 激励广告关闭
   * @param isEnded 广告是否结束
   */
  public static readonly REWARD_AD_CLOSED = 'ccams_n2s_reward_ad_closed';


  /**
   * 预加载插屏广告
   * @param posId 广告位 id
   */
  public static readonly PRELOAD_INTERSTITIAL_AD = 'ccams_s2n_preload_interstitial_ad';
  /**
   * 展示插屏广告
   * @param posId 广告位 id
   */
  public static readonly SHOW_INTERSTITIAL_AD = 'ccams_s2n_show_interstitial_ad';
}

