import { log } from "cc";
import { BaseMiniGameAd } from "./BaseMiniGameAd";

export class DouYinMiniGameAd extends BaseMiniGameAd {
  public showRewardAd(adUnitId: string, loadCallback: (err: Error) => void, closeCallback: (err: Error, res: { isEnded: boolean; }) => void): void {
    const rewardAd = tt.createRewardedVideoAd({
      adUnitId,
    });
    rewardAd.load()
      .then(() => {
        log('Reward ad loaded success.');
        loadCallback(null);

        rewardAd.onClose(result => {
          log('Reward ad loaded closed.');
          closeCallback(null, { isEnded: result?.isEnded });
          rewardAd.destroy();
        });
        rewardAd.show()
          .then(() => {
            log('Reward ad show success.');
          })
          .catch(err => {
            log('Reward ad show failed.', JSON.stringify(err));
            closeCallback(err, null);
          });
      })
      .catch((err: any) => {
        log('Reward ad loaded fail.', JSON.stringify(err));
        loadCallback(err);
      });
  }

  public showInterstitialAd(adUnitId: string): void {
    const interstitialAd = tt.createInterstitialAd({
      adUnitId,
    });
    interstitialAd.load()
      .then(() => {
        log('Interstitial ad load success.');
        interstitialAd.onClose(() => {
          interstitialAd.destroy();
        })
        interstitialAd.show()
          .then(() => {
            log('Interstitial ad show success.');
          })
          .catch(err => {
            log('Interstitial ad show failed.', JSON.stringify(err));
          });
      })
      .catch(err => {
        log("Interstitial ad load failed, catch err: ", JSON.stringify(err));
      });
  }
}


