import { _decorator, Component, Node } from 'cc';
import { DialogCtrl } from './DialogCtrl';
import { assetManager, Prefab, instantiate, Label, tween, Vec3 } from 'cc';
import { director } from 'cc';
import { log } from 'cc';

export class DialogMgr {
  public static show<T extends DialogCtrl>(bundle: string,
    dialogPrefab: string,
    componentType: { new(): T },
    data: any = null,
    callback: (code: number, data: any) => void = null) {
    // 限制了 Canvas 命名
    const canvasNode = director.getScene().getChildByName('Canvas');
    let diaglogMgrNode = canvasNode.getChildByName('DialogMgr');
    if (diaglogMgrNode == null) {
      diaglogMgrNode = new Node('DialogMgr');
      diaglogMgrNode.setParent(canvasNode);
    }

    assetManager.getBundle(bundle).load<Prefab>(dialogPrefab, (err, prefab) => {
      if (err) {
        log(err);
        return;
      }

      const dialog = instantiate(prefab);
      diaglogMgrNode.addChild(dialog);
      dialog.setPosition(Vec3.ZERO);

      const dialogCtrl: DialogCtrl = dialog.getComponent(componentType);
      dialogCtrl.setData(data);
      dialogCtrl.setCallback(callback);
    })
  }
}


