import { log } from 'cc';
import { Label, RichText } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { DialogCtrl } from './DialogCtrl';
const { ccclass, property } = _decorator;

@ccclass('WebTextDialogCtrl')
export class WebTextDialogCtrl extends DialogCtrl {
  @property(Node)
  public loading: Node = null;

  @property(Label)
  public titleLabel: Label = null;

  @property(RichText)
  public contentRichText: RichText = null;

  protected start(): void {
    this.loading.active = true;

    const { title, url } = this._data;
    this.titleLabel.string = title;

    // 创建一个新的 XMLHttpRequest 对象
    var xhr = new XMLHttpRequest();

    // 配置请求类型、请求的 URL 以及是否异步处理请求
    xhr.open('GET', url, true);

    // 设置请求完成后的回调函数
    xhr.onreadystatechange = () => {
      this.loading.active = false;

      // 检查请求状态
      if (xhr.readyState === 4) { // 4 表示请求已完成，并且响应已就绪
        if (xhr.status === 200) { // 200 表示请求成功
          // 请求成功，处理返回的数据
          log(xhr.responseText); // 打印响应文本
          this.contentRichText.string = xhr.responseText;
        } else {
          // 请求失败，处理错误
          this.contentRichText.string = '请求失败，状态码：' + xhr.status;
        }
      }
    };

    // 发送请求
    xhr.send();
  }

  private onBackButtonClicked() {
    this.close();
  }
}

