import { _decorator, Component, director, EventKeyboard, Input, input, KeyCode, log, native } from 'cc';
import { SDKBridge } from '../sdk/SDKBridge';
const { ccclass } = _decorator;

@ccclass('GlobalBackListener')
export class GlobalBackListener extends Component {
  protected onLoad(): void {
    director.addPersistRootNode(this.node);
    log('注册返回按键事件');
    input.on(Input.EventType.KEY_DOWN, this.onKeyDown, this);
  }

  protected onDestroy(): void {
    input.off(Input.EventType.KEY_DOWN, this.onKeyDown, this);
  }

  private onKeyDown(event: EventKeyboard) {
    if (event.keyCode === KeyCode.BACKSPACE) {
      log('按下返回键', event.keyCode);
      native.jsbBridgeWrapper.dispatchEventToNative(SDKBridge.EXIT_GAME);
    }
  }
}


