import { _decorator, Canvas, Color, Component, director, Graphics, Label, Layers, log, Node, tween, UITransform, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

export class Toast {
  public static DEFAULT_DURATION = 2;
  public static DEFAULT_GRAVITY = "BOTTOM";
  public static DEFAULT_BG_COLOR = new Color(102, 102, 102, 200);
  public static DEFAULT_FONT_SIZE = 36;

  public static async show(text: string,
    gravity: string = Toast.DEFAULT_GRAVITY,
    duration: number = Toast.DEFAULT_DURATION,
    bgColor: Color = Toast.DEFAULT_BG_COLOR) {
    const canvas = director.getScene().getComponentInChildren(Canvas);
    const canvasTransform = canvas.getComponent(UITransform);
    const width = canvasTransform.width;
    const height = canvasTransform.height;

    const bgNode = new Node("Toast");
    bgNode.layer = Layers.Enum.UI_2D;

    // Lable文本格式设置
    const textNode = new Node("Text");
    textNode.layer = Layers.Enum.UI_2D;

    const textLabel = textNode.addComponent(Label);
    textLabel.horizontalAlign = Label.HorizontalAlign.CENTER;
    textLabel.verticalAlign = Label.VerticalAlign.CENTER;
    textLabel.fontSize = Toast.DEFAULT_FONT_SIZE;
    textLabel.string = text;

    // 当文本宽度过长时，设置为自动换行格式
    if (text.length * textLabel.fontSize > (width * 3) / 5) {
      textNode.getComponent(UITransform).width = (width * 3) / 5;
      textLabel.overflow = Label.Overflow.RESIZE_HEIGHT;
    } else {
      textNode.getComponent(UITransform).width = text.length * textLabel.fontSize;
    }
    var lineCount =
      ~~((text.length * textLabel.fontSize) / ((width * 3) / 5)) + 1;
    textNode.getComponent(UITransform).height = textLabel.fontSize * lineCount;

    // 背景设置
    var ctx = bgNode.addComponent(Graphics);
    ctx.arc(
      -textNode.getComponent(UITransform).width / 2,
      0,
      textNode.getComponent(UITransform).height / 2 + 20,
      0.5 * Math.PI,
      1.5 * Math.PI,
      true
    );
    ctx.lineTo(textNode.getComponent(UITransform).width / 2, -(textNode.getComponent(UITransform).height / 2 + 20));
    ctx.arc(
      textNode.getComponent(UITransform).width / 2,
      0,
      textNode.getComponent(UITransform).height / 2 + 20,
      1.5 * Math.PI,
      0.5 * Math.PI,
      true
    );
    ctx.lineTo(-textNode.getComponent(UITransform).width / 2, textNode.getComponent(UITransform).height / 2 + 20);
    ctx.fillColor = bgColor;
    ctx.fill();

    canvas.node.addChild(bgNode);

    bgNode.addChild(textNode);

    // gravity 设置Toast显示的位置
    if (gravity === "CENTER") {
      bgNode.y = 0;
      bgNode.x = 0;
    } else if (gravity === "TOP") {
      bgNode.position = new Vec3(0, bgNode.position.y + (height / 5) * 2, 0);
    } else if (gravity === "BOTTOM") {
      bgNode.position = new Vec3(0, bgNode.position.y - (height / 5) * 2, 0);
    }

    tween(bgNode)
      .delay(duration)
      .call(() => {
        bgNode.destroy();
      }).start();
  }
}


