import { _decorator, clamp, Color, Component, EventMouse, EventTouch, instantiate, log, math, Node, Prefab, Size, Sprite, UITransform, Vec2, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

const CHESSBOARD_SIZE_5X5 = 916;
const CHESSBOARD_SIZE_10X10 = 1036;
const CHESSBOARD_SIZE_15X15 = 1106;

const CELL_SIZE_5X5 = 180;
const CELL_SIZE_10X10 = 100;
const CELL_SIZE_15X15 = 70;

const DIVIDER_SIZE = 7;
const DIVIDER_DELTA = 6;

const CELL_PADDING = 4;
const CELL_SPACE = 4;

function formatDecimal(num: number, places: number): number {
  let multiplier = Math.pow(10, places);
  let integer = Math.floor(num * multiplier);
  return integer / multiplier;
}

@ccclass('LevelChessBoardCtrl')
export class LevelChessBoardCtrl extends Component {
  @property(Prefab)
  public cellPrefab: Prefab = null;

  @property(Prefab)
  public dividerPrefab: Prefab = null;

  @property(Node)
  public cellsContainer: Node = null;

  @property(Node)
  public dividersContainer: Node = null;

  /**
   * 棋盘宽度（即几列）
   */
  private _width: number = 0;
  /**
   * 棋盘高度（即几行）
   */
  private _height: number = 0;
  /**
   * 格子大小
   */
  private _cellSize: number = 1;
  /**
   * 格子节点
   */
  private _cells: Node[][];
  /**
   * 当前颜色
   */
  private _currentColor: Color = Color.WHITE;
  /**
   * 原图像素
   */
  private _pixels: Color[][];
  /**
   * 谜题像素
   */
  private _puzzlePixels: Color[][];

  private _puzzleMode: boolean = false;

  public newLevel(width: number, height: number) {
    this._width = width;
    this._height = height;

    const uiTrans = this.cellsContainer.getComponent(UITransform);
    this._cellSize = formatDecimal((uiTrans.contentSize.width - (width - 1) * CELL_SPACE - 2 * CELL_PADDING) / width, 2) - 0.005;

    const chessboardSize = new Size(this._cellSize * this._width, this._cellSize * this._height);
    log(this._width, this._height, this._cellSize, chessboardSize);
    this.newCells(this._cellSize, this._width);

    setTimeout(() => {
      this._cells = Array.from({ length: this._height },
        () => Array(this._width));
      this._pixels = Array.from({ length: this._height }, 
        () => Array(this._width).fill(Color.WHITE));

      for (let i = 0; i < this.cellsContainer.children.length; i++) {
        const r = Math.floor(i / this._width);
        const c = Math.floor(i % this._width);
        this._cells[r][c] = this.cellsContainer.children[i];
      }
      this.updateCells(this._pixels);
    }, 100);
  }

  /**
   * 新建格子
   * @param chessboardSize 
   * @param cellSize 
   * @param cellCount 
   */
  private newCells(cellSize: number, cellCountPerRow: number) {
    this.cellsContainer.destroyAllChildren();

    const cellCount = cellCountPerRow * cellCountPerRow;
    for (let i = 0; i < cellCount; i++) {
      const cell = instantiate(this.cellPrefab);
      const trans = cell.getComponent(UITransform);
      trans.setContentSize(new Size(cellSize, cellSize));
      cell.setParent(this.cellsContainer);
    }
  }

  public setPuzzleMode(puzzleMode: boolean) {
    this._puzzleMode = puzzleMode;
    if (puzzleMode) {
      if (!this._puzzlePixels) {
        // 克隆原图像素
        this.syncPixels();
      }
      log(this._puzzlePixels);
      this.updateCells(this._puzzlePixels);
    } else {
      this.updateCells(this._pixels);
    }
  }

  public syncPixels() {
    if (!this._pixels) {
      return;
    }

    this._puzzlePixels = Array.from({ length: this._height },
      () => Array(this._width));
    for (let r = 0; r < this._height; r++) {
      for (let c = 0; c < this._width; c++) {
        this._puzzlePixels[r][c] = this._pixels[r][c].equals(Color.WHITE) ? Color.WHITE : Color.BLACK;
      }
    }
    this.updateCells(this._puzzlePixels);
  }

  public get puzzlePixels(): boolean[][] {
    const pixels: boolean[][] = Array.from({ length: this._height },
      () => Array(this._width));
    for (let r = 0; r < this._height; r++) {
      for (let c = 0; c < this._width; c++) {
        const cellSprite = this._cells[r][c].getComponent(Sprite);
        pixels[r][c] = !cellSprite.color.equals(Color.WHITE);
      }
    }
    return pixels;
  }

  public setColor(color: Color) {
    this._currentColor = color;
  }

  protected start(): void {
    this.node.on(Node.EventType.MOUSE_DOWN, this.onMouseEvent, this);
    this.node.on(Node.EventType.MOUSE_MOVE, this.onMouseEvent, this);
  }

  protected onDestroy(): void {
    this.node.off(Node.EventType.MOUSE_DOWN, this.onMouseEvent, this);
    this.node.off(Node.EventType.MOUSE_MOVE, this.onMouseEvent, this);
  }

  private onMouseEvent(event: EventMouse) {
    const touchPoint = event.getUILocation();
    const transform = this.cellsContainer.getComponent(UITransform);
    let nodePos = transform.convertToNodeSpaceAR(new Vec3(touchPoint.x, touchPoint.y));
    const boardPos = new Vec2(nodePos.x + transform.contentSize.width / 2, nodePos.y - transform.contentSize.height / 2);
    const rIdx = Math.floor(Math.abs(boardPos.y) / this._cellSize)
    const cIdx = Math.floor(boardPos.x / this._cellSize)

    if (rIdx >= this._height || cIdx >= this._width) {
      return;
    }

    const cellSprite = this._cells[rIdx][cIdx].getComponent(Sprite);
    if (event.getButton() === EventMouse.BUTTON_LEFT ||
        event.getButton() === EventMouse.BUTTON_RIGHT) {
      let color = Color.WHITE;
      if (event.getButton() === EventMouse.BUTTON_LEFT) {
        color = this._puzzleMode ? Color.BLACK : this._currentColor;
      }

      cellSprite.color = color;
      if (this._puzzleMode) {
        this._puzzlePixels[rIdx][cIdx] = color;
      } else {
        this._pixels[rIdx][cIdx] = color;
      }
    }
  }

  private updateCells(pixels: Color[][]) {
    for (let r = 0; r < this._height; r++) {
      for (let c = 0; c < this._width; c++) {
        const cellSprite = this._cells[r][c].getComponent(Sprite);
        cellSprite.color = pixels[r][c];
      }
    }
  }
}


