import { log, resources, TextAsset } from 'cc';
import { NonogramSolution } from './NonogramSolution';

export class NonogramDescription {
  private _width: number;
  private _height: number;
  private _columnDescriptions: number[][];
  private _rowDescriptions: number[][];

  constructor(columnDescriptions: number[][], rowDescriptions: number[][]) {
    this._width = columnDescriptions.length;
    this._height = rowDescriptions.length;
    this._columnDescriptions = columnDescriptions;
    this._rowDescriptions = rowDescriptions;
  }

  public static createWithSolution(solution: NonogramSolution): NonogramDescription {
    const width = solution.width;
    const height = solution.height;
    log(width, height);

    const columnDescriptions = Array(width).fill(0).map(() => Array(height).fill(0));
    const rowDescriptions = Array(height).fill(0).map(() => Array(width).fill(0));

    for (let x = 0; x < width; x++) {
      columnDescriptions[x] = solution.getColumnDescription(x);
    }
    for (let y = 0; y < height; y++) {
      rowDescriptions[y] = solution.getRowDescription(y);
    }

    return new NonogramDescription(columnDescriptions, rowDescriptions);
  }

  static async fromFile(file: string): Promise<NonogramDescription> {
    return new Promise((resolve, reject) => {
      resources.load(file, TextAsset, (err, data) => {
        if (err) {
          reject(err);
          return;
        }

        const lines = data!.text.split(/\r?\n/)
          .filter(line => line.trim().length > 0);
        const cols: number[][] = [];
        const rows: number[][] = [];
        let mode: 'width' | 'height' | 'rows' | 'columns' | 'goal' | '' = '';

        for (const line of lines) {
          const parts = line.split(' ');
          if (parts[0] === 'width') {
            mode = 'width';
          } else if (parts[0] === 'height') {
            mode = 'height';
          } else if (parts[0] === 'rows') {
            mode = 'rows';
          } else if (parts[0] === 'columns') {
            mode = 'columns';
          } else if (parts[0] === 'goal') {
            mode = 'goal';
          } else if (mode === 'rows') {
            rows.push(parseIntegerList(line));
          } else if (mode === 'columns') {
            cols.push(parseIntegerList(line));
          }
        }
        resolve(new NonogramDescription(cols, rows));
      });
    });
  }

  public get width(): number {
    return this._width;
  }

  public get height(): number {
    return this._height;
  }

  public getRowDescription(y: number): number[] {
    return this._rowDescriptions[y];
  }

  public getColumnDescription(x: number): number[] {
    return this._columnDescriptions[x];
  }

  public equals(other: NonogramDescription): boolean {
    if (this._width !== other._width || this._height !== other._height) {
      return false;
    }
    return this._rowDescriptions.every((row, index) => row.every((value, i) => value === other._rowDescriptions[index][i])) &&
      this._columnDescriptions.every((col, index) => col.every((value, i) => value === other._columnDescriptions[index][i]));
  }

  public get descriptionHeight(): number {
    return Math.max(...this._columnDescriptions.map(col => col.length));
  }

  public get descriptionWidth(): number {
    return Math.max(...this._rowDescriptions.map(row => row.length));
  }
}

// Utility functions to parse and write integer lists from/to strings
function parseIntegerList(line: string): number[] {
  return line.split(',').map(s => parseInt(s.trim(), 10)).filter(num => num !== 0);
}

function writeIntegerList(arrays: number[][]): string {
  return arrays.map(array => array.join(',')).join('\n');
}
